* {
  padding: 0;
  margin: 0;
  box-sizing: content-box;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.wrapper {
  text-align: center;
  .nav {
    padding: 10px;
    font-size: 1.2rem;
    .link {
      margin: 0 auto;
      text-decoration: none;

      margin: 10px;
      font-weight: bold;
      color: dimgrey;
      cursor: pointer;
      &:hover {
        color: black;
      }
    }
  }
}
